.container {
    padding: 0 20px;
    margin: 0 auto;

    @media screen and (min-width: 432px) {
      max-width: 432px;
    }

    @media screen and (min-width: 768px) {
      max-width: 768px;
      width: 768px;
      padding: 0 32px;
    }

    @media screen and (min-width: 1140px) {
      max-width: 1140px;
      width: 1140px;
      padding: 0 16px;
    }

  }